import { NgModule, OnInit  } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { manageCustomerComponent } from './manageCustomer.component';
import { manageCustomerRoutes } from './manageCustomer.routing';


@NgModule({
    imports: [
        CommonModule,
        RouterModule.forChild(manageCustomerRoutes),
        FormsModule
    ],
    declarations: [manageCustomerComponent]
})

export class manageCustomerModule {}


